* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.shop {
  min-height: 100vh;
  background-color: gray;
  display: grid;
}

h2 {
  text-align: center;
}

.login {
  background-color: gray;
}

#back {
  justify-content: center;
  align-items: center;
  display: flex;
}

.product-grid {
  width: 80%;
  background-color: gray;
  grid-template-rows: auto;
  grid-template-columns: repeat(3, 1fr);
  margin: 5% auto auto;
  display: grid;
}

.products {
  height: 300px;
  background-color: #fff;
  border-radius: 20px;
  grid-template-rows: auto;
  justify-items: center;
  margin: 10px auto;
  display: grid;
}

.shoppingcart {
  text-align: center;
  background-color: gray;
}

.fa-stack[data-count]:after {
  content: attr(data-count);
  color: #fff;
  text-align: center;
  min-width: 1em;
  background: #ff0000d9;
  border-radius: 50%;
  padding: .6em;
  font-size: 30%;
  font-weight: bold;
  line-height: .8em;
  position: absolute;
  top: 1%;
  right: 0%;
}

.fa-stack {
  margin-left: 90%;
}

button {
  color: #fff;
  background-color: #000;
  padding: 5px;
  font-weight: bold;
}

